const time = document.createElement('div');
const play = document.createElement('button');
const moves = document.createElement('div');

let playTime = false;
let timeId;
let hour = 0;
let min = 0;
let sec = 0;
let timeOut;

function playTimer() {
  if (!playTime) {
    showTime();
    play.innerHTML = 'pause';
    playTime = true;
  } else {
    clearTimeout(timeId);
    play.innerHTML = 'play';
    playTime = false;
  }
}

function showTime() {
  time.innerHTML = 'Time ' + `${addZero(min)}<span>:</span>${addZero(sec)}`;
  timeId = setTimeout(showTime, 1000);
  if (sec === 59) {
    sec = -1;
    min = min + 1;
    localStorage.setItem('minutes', min);
    localStorage.setItem('seconds', sec);
  }
  if (min === 59) {
    min = 0;
    hour = hour + 1;
  }
  localStorage.setItem('minutes', min);
  localStorage.setItem('seconds', sec);
  sec = sec + 1;
}

function addZero(n) {
  return (parseInt(n, 10) < 10 ? '0' : '') + n;
}

function getMoves(k) {
  moves.innerHTML = `Moves: ${k}`;
}

export { playTimer, showTime, addZero, getMoves };
export { play, time, moves, playTime, timeId, hour, min, sec};
