import {timeId, min, sec} from './counter.js';
import {addZero} from './counter.js';
import {k} from '../index.js';

const textWin = document.createElement('h1');

document.body.prepend(textWin);

function win() {
  clearTimeout(timeId);
  textWin.className = 'active';
  textWin.innerHTML = `You won <span>Time ${addZero(min)}:${addZero(sec)}
  </span> <span>Moves: ${k}</span>`;
  
  // textWin.innerHTML = `You won <span>${timeOut}</span> <span>Moves: ${k}</span>`;

  textWin.addEventListener('click', () => {
    textWin.classList.remove('active');
  });
}

export { win };