import './style.css';
import './sound.mp3';


import { soundPlay } from './module/sound.js';
import { win } from './module/gameWin.js';
import { playTimer, getMoves, addZero} from './module/counter.js';
import { play, time, moves, playTime } from './module/counter.js';
import { menu, arr, initList, openMenu, closeMenu  } from './module/menu.js';


let k = 0;
let min = 0;
let sec = 0;

export {sec, min, k}


const game = document.createElement('div');
game.className = 'game';
document.body.prepend(game);

const header = document.createElement('header');
game.append(header);

const btnMenu = document.createElement('button');
btnMenu.className = 'btn-menu';
btnMenu.innerHTML = 'Menu';
header.append(btnMenu);

// const time = document.createElement('div');
time.className = 'time';
time.innerHTML = 'Time ' + '00:00';
header.append(time);

// const moves = document.createElement('div');
moves.className = 'moves';
moves.innerHTML = 'Moves: 0';
header.append(moves);

// const play = document.createElement('button');
play.className = 'btn-play';
play.innerHTML = 'play';
header.append(play);

const container = document.createElement('div');
container.className = 'container';
game.append(container);

// const menu = document.createElement('ul');
menu.className = 'menu';
container.append(menu);

initList(arr);

const btnBack = document.createElement('button');
btnBack.className = 'btn-back';
btnBack.textContent = 'back';
container.append(btnBack);

const menuItem = document.querySelector('.menu__item');

menuItem.addEventListener('click', () => {
  newGame();
  min = 0;
  sec = 0;
  localStorage.setItem('minutes', min);
  localStorage.setItem('seconds', sec);
  openMenu();
});

const field = document.createElement('div');
field.className = 'field';
game.append(field);

let cellSize = 75;

const empty = {
  value: 0,
  top: 0,
  left: 0,
};

const cells = [];
cells.push(empty);

function move(index) {
  const cell = cells[index];

  const leftDiff = Math.abs(empty.left - cell.left);
  const topDiff = Math.abs(empty.top - cell.top);

  if (leftDiff + topDiff > 1 || !playTime) {
    return;
  }

  cell.element.style.left = `${empty.left * cellSize}px`;
  cell.element.style.top = `${empty.top * cellSize}px`;

  const emptyLeft = empty.left;
  const emptyTop = empty.top;

  empty.left = cell.left;
  empty.top = cell.top;

  cell.left = emptyLeft;
  cell.top = emptyTop;

  soundPlay();

  if (playTime) {
    k++;
    getMoves(k);
  }

  const isFinished = cells.every((cell) => {
    return cell.value === cell.top * 4 + cell.left;
  });

  if (isFinished) {
    win();
  }
}

const numbers = [...Array(15).keys()].sort(() => Math.random() - 0.5);

for (let i = 1; i <= 15; i++) {
  const cell = document.createElement('div');
  const value = numbers[i - 1] + 1;
  cell.className = 'cell';
  cell.innerHTML = value;

  const left = i % 4;
  const top = (i - left) / 4;

  cells.push({
    value: value,
    left: left,
    top: top,
    element: cell,
  });

  cell.style.left = `${left * cellSize}px`;
  cell.style.top = `${top * cellSize}px`;

  field.append(cell);

  cell.addEventListener('click', () => {
    move(i);
  });
}

function newGame() {
  k = 0;
  moves.innerHTML = `Moves: ${k}`;

  min = 0;
  sec = 0;
  time.innerHTML = 'Time ' + `${addZero(min)}<span>:</span>${addZero(sec)}`;

  field.innerHTML = '';
  cells.length = 0;
  empty.top = 0;
  empty.left = 0;
  cells.push(empty);

  
  const numbers = [...Array(15).keys()].sort(() => Math.random() - 0.5);
  for (let i = 1; i <= 15; i++) {
    const cell = document.createElement('div');
    const value = numbers[i - 1] + 1;
    cell.className = 'cell';
    cell.innerHTML = value;
    
    const left = i % 4;
    const top = (i - left) / 4;

    cells.push({
      value: value,
      left: left,
      top: top,
      element: cell,
    });

    cell.style.left = `${left * cellSize}px`;
    cell.style.top = `${top * cellSize}px`;

    field.append(cell);

    cell.addEventListener('click', () => {
      move(i);
    });
  }
}

play.addEventListener('click', playTimer);

btnMenu.addEventListener('click', function () {
  openMenu();
});

const solution = document.querySelector('#solution');
solution.addEventListener('click', function () {
  autoFinish();
});

function autoFinish() {
  moves.innerHTML = `Moves: ${k}`;
  time.innerHTML = 'Time ' + `${addZero(min)}<span>:</span>${addZero(sec)}`;

  field.innerHTML = '';
  cells.length = 0;
  empty.top = 0;
  empty.left = 0;
  cells.push(empty);


  const numbers = [...Array(15).keys()];

  for (let i = 1; i <= 15; i++) {
    const cell = document.createElement('div');
    const value = numbers[i - 1] + 1;
    cell.className = 'cell';
    cell.innerHTML = value;
    
    const left = i % 4;
    const top = (i - left) / 4;

    cells.push({
      value: value,
      left: left,
      top: top,
      element: cell,
    });

    cell.style.left = `${left * cellSize}px`;
    cell.style.top = `${top * cellSize}px`;

    field.append(cell);

    cell.addEventListener('click', () => {
      move(i);
    });
  }

  closeMenu();
  win();
  if (playTime) {
    play.innerHTML = 'play';
    playTime = false;
  }
}
