export { menu, arr };
export { initList, openMenu, closeMenu };

import {timeId, playTime} from './counter.js';
import {showTime} from './counter.js';

const menu = document.createElement('ul');


let arr = {
  newGame: 'New game',
  setting: 'Setting',
  bestScores: 'Best scores',
  rulesGame: 'Rules',
  solution: 'Solution',
};

function initList(arr) {
  for (let key in arr) {
    let li = document.createElement('li');
    li.className = 'menu__item';
    li.id = key;
    li.textContent = arr[key];
    menu.append(li);
  }
}

function openMenu() {
  menu.classList.toggle('active');
  if (menu.classList.contains('active')) {
    clearTimeout(timeId);
  } else if (!menu.classList.contains('active') && playTime == false) {
    return;
  } else {
    setTimeout(showTime, 1000);
  }
}

function closeMenu() {
  menu.classList.remove('active');
}
